.marketing-form__section {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.marketing-form__section.marketing-form__section--done .marketing-form__section__label::before {
    content: "";
    background-image: url(images/checked.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 0.5em 0.5em;
    height: 0.5em;
    width: 0.5em;
    background-color: #7f4156;
    padding: 0.1em;
}

.marketing-form__section .marketing-form__section__label {
    color: #ffffff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    font-size: 1.2vw;
}

.marketing-form__section .marketing-form__section__label::before {
    content: counter(section) ".";
    counter-increment: section;
    font-family: Shnobel, Helvetica Neue, Arial, sans-serif;
    color: #ffffff;
    font-size: 2em;
    font-weight: 300;
    margin-left: -1.5em;
    width: 0.8em;
    position: absolute;
}

.marketing-form__section .marketing-form__section__content {
    display: -ms-flexbox;
    display: flex;
}
.marketing-form__section .marketing-form__section__content fieldset {
    border: 0;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}



